/**
 * jQuery lightzoom v1.1.0
 */

#lz-container {
    display: block;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    white-space: nowrap;
    position: absolute;
    z-index: 9999990 !important;
}

#lz-box {
    position: fixed;
    height: 100%;
    width: 100%;
    line-height: 1px;
    text-align: center;
    display: inline-block;
    z-index: 9999998 !important;
}

#lz-overlay {
    display: block;
    opacity: 0.6;
    background: #000;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 9999991 !important;
}

#lz-box:before {
    content: "";
    display: inline-block;
    height: 50%;
    width: 1px;
    margin-right: -1px;
}

#lz-box img {
    float: none;
    display: inline-block;
    max-height: 80%;
    max-width: 100%;
    margin: 0;
    padding: 0;
    width: auto;
    height: auto;
    vertical-align: middle;
    opacity: 0;
    position: relative;
    z-index: 9999995 !important;
}

#lz-box p {
    position: relative;
    bottom: 0;
    margin-top: -30px;
    margin-bottom: 0;
    line-height: 20px;
    font-size: 18px;
    z-index: 9999997 !important;
}

#lz-close {
    position: absolute;
    top: 2%;
    right: 2%;
    width: 30px;
    height: 30px;
    padding: 0;
    font-size: 0;
    line-height: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    z-index: 9999999 !important;

    &::after,
    &::before {
        position: absolute;
        top: 50%;
        left: 0;
        content: "";
        display: block;
        width: 100%;
        height: 2px;
        border-radius: 8px;
        background-color: white;
    }

    &::after {
        transform: rotate(-45deg);
    }

    &::before {
        transform: rotate(45deg);
    }

    &:hover {
        opacity: 0.8;
    }

    &:active {
        opacity: 0.6;
    }
}

#lz-loading-center {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 9999999 !important;
}

#lz-loading-center > .lz-loading-center-box {
    position: fixed;
    left: 50%;
    top: 50%;
    height: 150px;
    width: 150px;
    margin-top: -75px;
    margin-left: -75px;
}

.lz-object {
    width: 20px;
    height: 20px;
    background-color: #fff;
    float: left;
    margin-right: 20px;
    margin-top: 65px;
    -moz-border-radius: 50% 50% 50% 50%;
    -webkit-border-radius: 50% 50% 50% 50%;
    border-radius: 50% 50% 50% 50%;
}

#lz-object_one {
    -webkit-animation: object_one 1.5s infinite;
    animation: object_one 1.5s infinite;
}

#lz-object_two {
    -webkit-animation: object_two 1.5s infinite;
    animation: object_two 1.5s infinite;
    -webkit-animation-delay: 0.25s;
    animation-delay: 0.25s;
}

#lz-object_three {
    -webkit-animation: object_three 1.5s infinite;
    animation: object_three 1.5s infinite;
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
}

@-webkit-keyframes object_one {
    75% {
        -webkit-transform: scale(0);
    }
}

@keyframes object_one {
    75% {
        transform: scale(0);
        -webkit-transform: scale(0);
    }
}

@-webkit-keyframes object_two {
    75% {
        -webkit-transform: scale(0);
    }
}

@keyframes object_two {
    75% {
        transform: scale(0);
        -webkit-transform: scale(0);
    }
}

@-webkit-keyframes object_three {
    75% {
        -webkit-transform: scale(0);
    }
}

@keyframes object_three {
    75% {
        transform: scale(0);
        -webkit-transform: scale(0);
    }
}
